.post-form {
    width: 400px;
    input {
        color: black!important; } }

.post-form_button {
    // background: greenyellow
    padding: 10px 20px;
    border-radius: 5px;
    transition: .3s;
    color: black;
    cursor: pointer; }


.post-form_button {
    background: linear-gradient(45deg, #2196F3 30%, #21CBF3 90%);
    // color: white!important
    &:hover {
        box-shadow: 0 3px 5px 2px rgba(33, 203, 243, .3); } }


.post-form_input-content {
    width: 100%;
    padding: 0;
    padding-left: 20px;
    margin: 0;
    border: 0;
    height: 30px;
    border-radius: 5px;
    margin-top: 10px; }


.post-form_input-title {
    width: 100%;
    padding: 0;
    padding-left: 20px;
    margin: 0;
    border: 0;
    height: 30px;
    border-radius: 5px;
    margin-top: 10px; }


.post-form_input-content, .post-form_input-title {
    &:focus, {
        outline-color: blue; } }


.inputList {
    margin-right: 30px; }

